.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

img {
  margin: 10px;
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

ul li {
  text-align: left;
  margin-left: 50px;
}

.avatar {
  height: 90px;
  border-radius: 50%;
  margin: 20px;
}

.pureComponent {
  padding: 1px;
  margin: 10px;
  border: 1px solid rgb(13, 112, 107);
  border-radius: 20px;
}